<template>
    <div>
        <div class="container po_r">
            <div class="home-menu">
                <span><a href="javascript:void(0);" @click="swiperToElement(1)" class="active"></a></span>
                <span><a href="javascript:void(0);" @click="swiperToElement(2)"></a></span>
                <span><a href="javascript:void(0);" @click="swiperToElement(3)"></a></span>
                <span><a href="javascript:void(0);" @click="swiperToElement(4)"></a></span>
                <span><a href="javascript:void(0);" @click="swiperToElement(5)"></a></span>
            </div>
        </div>
        <swiper :options="swiperOption" ref="mySwiper" class="home-slide">
            <!-- slides -->
            <swiper-slide class="stop-swiping bg1">
                <div class="swiperText">
                    <div class="swiperTextElement">
                        <h2></h2>
                        <p class="lh26">
                            25003001.5</p>
                        <p>
                            <router-link :to="{path:'/business/detail3'}" class="btn btnd">
                            </router-link>
                        </p>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide class="stop-swiping bg2">
                <div class="swiperText">
                    <div class="swiperTextElement">
                        <h2></h2>
                        <p class="lh26">2007500</p>
                        <p>
                            <router-link :to="{path:'/business/detail1'}" class="btn btnd">
                            </router-link>
                        </p>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide class="stop-swiping bg3">
                <div class="swiperText">
                    <div class="swiperTextElement">
                        <h2></h2>
                        <p class="lh26"></p>
                        <p>
                            <router-link :to="{path:'/business/detail2'}" class="btn btnd">
                            </router-link>
                        </p>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide class="stop-swiping bg5">
                <div class="swiperText">
                    <div class="swiperTextElement">
                        <h2></h2>
                        <p class="lh26"></p>
                        <p>
                            <router-link :to="{path:'/business/detail5'}" class="btn btnd">
                            </router-link>
                        </p>
                    </div>
                </div>
            </swiper-slide>
            <swiper-slide class="stop-swiping bg4">
                <div class="swiperText">
                    <div class="swiperTextElement">
                        <h2></h2>
                        <p class="lh26">
                            </p>
                        <p>
                            <router-link :to="{path:'/business/detail4'}" class="btn btnd">
                            </router-link>
                        </p>
                    </div>
                </div>
            </swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>

        <div class="container">
            <div class="blk70"></div>
            <div class="home-about">
                <em><img src="../../static/images/018.jpg" alt=""></em>
                <div class="info">
                    <div class="font18 lh50 pb-10"></div>
                    <div class="c999 lh24" v-html="info"></div>
                    <div class="clearfix pt-30">
                        <router-link to="/about" class="btn btn-default"></router-link>
                    </div>
                </div>
            </div>
            <div class="blk100"></div>
        </div>

    </div>
</template>

<script>
	import "swiper/dist/css/swiper.css"
	import { swiper, swiperSlide } from "vue-awesome-swiper"
	import request from "../assets/js/request"

	export default {
		name: "Home",
		data () {
			return {
				info: "",
				swiperOption: {
					notNextTick: true,
					loop: true,
					centeredSlides: true,
					spaceBetween: 30,
					noSwiping: true,
					noSwipingClass: "stop-swiping",
					autoplay: true,
					debugger: true,
					pagination: {
						el: ".swiper-pagination",
						clickable: true
					},
					on: {
						autoplay: () => {
							let swiper = this.$refs.mySwiper.swiper
							let i = swiper.activeIndex
							$(".home-menu a").removeClass("active")
							$(".home-menu span").eq(i - 1).find("a").addClass("active")
						},
						click: () => {
							let swiper = this.$refs.mySwiper.swiper
							let i = swiper.activeIndex
							$(".home-menu a").removeClass("active")
							$(".home-menu span").eq(i - 1).find("a").addClass("active")
						}
					}
				}
			}
		},
		computed: {
			swiper () {
				return this.$refs.mySwiper.swiper
			}
		},
		mounted () {
			let that = this
			$(".header").show()
			$(".loginHeader").hide()
			$(document).on("click", ".home-menu a", function () {
				$(this).parent().siblings("span").find("a").removeClass("active")
				$(this).addClass("active")
			})
			request.getRequest("GeCompanyIntro", {}).then(res => {
				let text = res.Data.DocSummary.replace(/<\/?[^>]*>/g, "")
				let text1 = text.replace(/[|]*\n/, "")
				let text2 = text1.replace(/&nbsp;/ig, "")
				//that.info = text2.substr(0, 120) + "..."
				that.info = res.Data.DocSummary
			})
		},
		methods: {
			swiperToElement (len) {
				this.swiper.slideTo(len, 1000, false)
			}
		},
		components: {
			swiper, swiperSlide
		}
	}
</script>



