<script>
	import request from "@/assets/js/request"

	export default {
		name: "Business",
		data () {
			return {
				title: "",
				data: "",
				divShow: 3,
				modalW: "",
				modalH: "",
				modalHH: ""
			}
		},
		mounted () {
			let $this = this
			$this.gettype($this.$route.name)
			$this.$nextTick(() => {
				$this.modalW = "width:" + ($(window).width() - 240) + "px"
				$this.modalH = "height:" + ($(window).height() - 140) + "px; overflow-y:auto;"
				$this.modalHH = "height:" + ($(window).height() - 140 - 120) + "px; overflow-y:auto;"
				$(window).on("resize load", function () {
					$this.modalW = "width:" + ($(window).width() - 240) + "px"
					$this.modalH = "height:" + ($(window).height() - 140) + "px; overflow-y:auto;"
					$this.modalHH = "height:" + ($(window).height() - 140 - 120) + "px; overflow-y:auto;"
				})
			})
		},
		methods: {
			getInfo (names) {
				let self = this
				request.getRequest("GetBusinessinfo", {name: names}).then(res => {
					self.data = res.Data
				})
			},
			gettype (id) {
				switch (id) {
					case "detail1" :
						this.title = ""
						this.divShow = 1
						break
					case "detail2" :
						this.title = ""
						this.divShow = 2
						break
					case "detail3" :
						this.title = ""
						this.divShow = 3
						break
					case "detail4" :
						this.title = ""
						this.divShow = 4
						break
					case "detail5" :
						this.title = ""
						this.divShow = 5
						break
				}
				if (this.title === "") {
					this.getInfo("")
				} else {
					this.getInfo(this.title)
				}
			}
		},
		watch: {
			$route (to, from) {
				this.gettype(to.name)
			}
		}
	}
</script>

